
main.Home {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 26px;
  background-color: #20222a;
  font-family: 'Arial';
  color: #FFFFFF;

  .content {
    z-index: 1;
    text-align: center;
  }

  .menu_down {
    background: #330000;
    position: absolute;
    left: 0%;
    right: 0%;
    bottom: 0px;
    height: 40px;
    bottom: 0%;
    display: flex;
    justify-content: space-around;
    align-items: space-around;
  }

  .buttonHome {
    background: #374C5F;
    width: 20%;
    border: none;
    color: #FFFFFF;

    img {
      width: 20px;
    }
  }
  
  .back_lane_1, .back_lane_2{
    background: #6e2b2b;
    position: fixed;
    right: 0;
    top: 0;
    bottom: 0;
    left: 50px;
    transform: rotate(315deg);
    // opacity: 0.12;
    width:200%;
  }

  .back_lane_1{
  
  }
  
  .back_lane_2{
    left: 250px;
    background-color: #212530;

  }
}
